import React from 'react';
import PropTypes from 'prop-types';
import BlcTitleBase from 'components/BlcTitle/BlcTitleBase';
import { color, size } from 'public/styles';

const blcH6 = props => (
	<BlcTitleBase>
		<h6 id={props.id}>
			{props.children}

			<style jsx>{`
				color: ${
	props.inverted
		? (props.color
			? props.color
			: color.white)
		: (props.color
			? props.color
			: color.asphalt)
	};
				font-size: ${size.h6};
				line-height: ${size.h6LineHeight};
				margin: ${props.margin} 0;
				text-align: ${props.textAlign};
				white-space: ${props.nowrap ? 'nowrap' : 'normal'};
			`}</style>
		</h6>
	</BlcTitleBase>
);

blcH6.propTypes = {
	children: PropTypes.node,
	color: PropTypes.string,
	id: PropTypes.string,
	inverted: PropTypes.bool,
	margin: PropTypes.string,
	nowrap: PropTypes.bool,
	textAlign: PropTypes.oneOf(['left', 'center', 'right'])
}

blcH6.defaultProps = {
	children: null,
	id: null,
	inverted: false,
	margin: '0',
	nowrap: false,
	textAlign: 'left'
}

export default blcH6;
