import React from 'react';
import PropTypes from 'prop-types';
import { color, util, weight } from 'public/styles';

const blcText = props => (
	<span>
		{props.children}

		<style jsx>{`
			color: ${
	props.inverted
		? (props.color
			? props.color
			: color.white)
		: (props.color
			? props.color
			: color.asphalt)
	};
			display: ${props.display};
			font-size: ${util.blcParagraphSize(props.size)};
			font-weight: ${weight.regular};
			line-height: ${util.blcParagraphLineHeight(props.size)};
			margin: ${props.margin};
			text-align: ${props.textAlign};
		`}</style>
	</span>
);

blcText.propTypes = {
	children: PropTypes.node,
	color: PropTypes.string,
	display: PropTypes.oneOf(['inline', 'inline-block', 'block']),
	inverted: PropTypes.bool,
	margin: PropTypes.string,
	size: PropTypes.oneOf(['largest', 'larger', 'smaller', 'smallest', 'inherit']),
	textAlign: PropTypes.oneOf(['left', 'center', 'right'])
}

blcText.defaultProps = {
	children: null,
	display: 'inline',
	inverted: false,
	margin: '0',
	size: 'inherit',
	textAlign: 'left'
}

export default blcText;
