import React from 'react';
import PropTypes from 'prop-types';
import { color, util, weight } from 'public/styles';

const blcLabel = props => (
	<label htmlFor={props.for}>
		{props.children}

		<style jsx>{`
			color: ${
	props.inverted
		? (props.color
			? props.color
			: color.smokeLight)
		: (props.color
			? props.color
			: color.grayDark)
	};
			cursor: ${props.cursor};
			display: ${props.display};
			font-family: MarkPro;
			font-size: ${util.blcParagraphSize(props.size)};
			font-stretch: normal;
			font-style: normal;
			font-weight: ${weight.bold};
			letter-spacing: normal;
			line-height: ${util.blcParagraphLineHeight(props.size)};
			order: ${props.order};
			padding: ${props.padding} 0;
			text-align: ${props.textAlign};
			width: 100%;
		`}</style>
	</label>
);

blcLabel.propTypes = {
	children: PropTypes.node,
	color: PropTypes.string,
	cursor: PropTypes.oneOf(['default', 'pointer', 'not-allowed']),
	display: PropTypes.oneOf(['inline', 'inline-block', 'block', 'flex']),
	for: PropTypes.string,
	inverted: PropTypes.bool,
	order: PropTypes.number,
	padding: PropTypes.string,
	size: PropTypes.oneOf(['largest', 'larger', 'smaller', 'smallest', 'inherit']),
	textAlign: PropTypes.oneOf(['left', 'center', 'right'])
}

blcLabel.defaultProps = {
	children: null,
	cursor: 'default',
	display: 'block',
	for: null,
	inverted: false,
	order: 0,
	padding: '0',
	size: 'smaller',
	textAlign: 'left'
}

export default blcLabel;
