import React from 'react';
import PropTypes from 'prop-types';
import BlcLink from 'components/BlcText/Link'

const BlcLogoBase = props => (
	<h1>
		<BlcLink href={props.href}>
			{props.children}
		</BlcLink>

		<style jsx>{`
			display: inline-block;
			font-size: 0;
			height: fit-content;
			margin: ${props.margin};
			width: fit-content;

			a {
				display: block;
				height: fit-content;
				width: fit-content;
			}
		`}</style>
	</h1>
);

BlcLogoBase.propTypes = {
	children: PropTypes.node,
	href: PropTypes.string,
	margin: PropTypes.string
}

BlcLogoBase.defaultProps = {
	href: '/',
	margin: '0'
}

export default BlcLogoBase;
