import React from 'react';
import BaseComponents from 'gatsby-theme-docz/src/components'

import {
	BlcParagraph,
	BlcQuote,
	BlcList,
	BlcListItem,
	BlcCode,
	Bold,
	Italic,
	Br,
	BlcLink
} from 'components/BlcText';

import { BlcH1, BlcH2, BlcH3, BlcH4, BlcH5, BlcH6 } from 'components/BlcTitle';

import { size } from 'public/styles';

// Default components with props.
const blcH1Gatsby = props => {
	var { margin, ...other } = props;
	return <BlcH1 margin={size.marginLargest} {...other}>{props.children}</BlcH1>
};

const blcH2Gatsby = props => {
	var { margin, ...other } = props;
	return <BlcH2 margin={size.marginLargest} {...other}>{props.children}</BlcH2>
};

const blcH3Gatsby = props => {
	var { margin, ...other } = props;
	return <BlcH3 margin={size.marginLarger} {...other}>{props.children}</BlcH3>
};

const blcH4Gatsby = props => {
	var { margin, ...other } = props;
	return <BlcH4 margin={size.margin} {...other}>{props.children}</BlcH4>
};

const blcH5Gatsby = props => {
	var { margin, ...other } = props;
	return <BlcH5 margin={size.marginSmaller} {...other}>{props.children}</BlcH5>
};

const blcH6Gatsby = props => {
	var { margin, ...other } = props;
	return <BlcH6 margin={size.marginSmallest} {...other}>{props.children}</BlcH6>
};

const blcCodeGatsby = props => {
	return <BlcCode>{props.children}</BlcCode>
};

const blcPreGatsby = props => {
	return <BlcCode type="snippet">{props.children}</BlcCode>
};

const blcUlGatsby = props => {
	return <BlcList>{props.children}</BlcList>
};

const blcOlGatsby = props => {
	return <BlcList type="ordered">{props.children}</BlcList>
};

export default {
	...BaseComponents,
	p:      	BlcParagraph,
	a:			BlcLink,
	blockquote: BlcQuote,
	strong:		Bold,
	em:			Italic,
	ul:			blcUlGatsby,
	ol:			blcOlGatsby,
	li:			BlcListItem,
	br: 		Br,
	code:		blcCodeGatsby,
	pre:		blcPreGatsby,
	h1: 		blcH1Gatsby,
	h2: 		blcH2Gatsby,
	h3:     	blcH3Gatsby,
	h4:     	blcH4Gatsby,
	h5:     	blcH5Gatsby,
	h6:     	blcH6Gatsby,
}
