import React from 'react';
import PropTypes from 'prop-types'
import BlcLogoBase from 'components/BlcLogo/BlcLogoBase';
import BlcLogoImage from 'components/BlcLogo/BacklotCars/BlcLogoImage';
import { color } from 'public/styles';

const blcLogo = props => (
	<BlcLogoBase margin={props.margin}>
		<BlcLogoImage size={props.size} color={props.color} />
	</BlcLogoBase>
);

blcLogo.propTypes = {
	color: PropTypes.oneOf([color.asphalt, color.backlot, color.white]),
	margin: PropTypes.string,
	size: PropTypes.number
}

blcLogo.defaultProps = {
	color: color.white,
	margin: '0',
	size: 72
}

export default blcLogo;
