import React from 'react';
import PropTypes from 'prop-types';
import { color, size, util } from 'public/styles';

const blcList = props => {
	if (props.type === 'ordered') {
		return (
			<ol>
				{props.children}

				<style jsx>{`
					color: ${props.color};
					display: block;
					list-style-type: decimal;
					font-size: ${util.blcParagraphSize(props.size)};
					line-height: ${util.blcParagraphLineHeight(props.size)};
					margin: 0;
					padding: 0 0 0 ${size.marginLarger};
					text-align: ${props.textAlign};
				`}</style>
			</ol>
		)
	} else {
		return (
			<ul>
				{props.children}

				<style jsx>{`
					color: ${props.color};
					display: block;
					list-style-type: disc;
					font-size: ${util.blcParagraphSize(props.size)};
					line-height: ${util.blcParagraphLineHeight(props.size)};
					margin: 0;
					padding: 0 0 0 ${size.marginLarger};
					text-align: ${props.textAlign};
				`}</style>
			</ul>
		)
	}
}

blcList.propTypes = {
	children: PropTypes.node,
	color: PropTypes.string,
	size: PropTypes.oneOf(['largest', 'larger', 'smaller', 'smallest', 'inherit']),
	textAlign: PropTypes.oneOf(['left', 'center', 'right']),
	type: PropTypes.oneOf(['ordered', 'unordered'])
}

blcList.defaultProps = {
	children: null,
	color: color.asphalt,
	size: null,
	textAlign: 'left',
	type: 'unordered'
}

export default blcList;
