import React from 'react';
import PropTypes from 'prop-types';
import { color } from 'public/styles';
import { BlcLinkStyleWrap } from 'components/BlcText/Link/BlcLinkStyleWrap'
import GatsbyLink from "gatsby-link"

const BlcLink = props => {
	const external = /(#|https?:\/\/)/i.test(props.href);
	if (external) {
		return (
			<a href={props.href} target={props.target}>
				<BlcLinkStyleWrap {...props}>
					{props.children}

				</BlcLinkStyleWrap>
			</a>)
	}
	else {
		return (
			<GatsbyLink to={props.href}>
				<BlcLinkStyleWrap {...props}>
					{props.children}

				</BlcLinkStyleWrap>
			</GatsbyLink>
		)
	}
}


BlcLink.propTypes = {
	children: PropTypes.node,
	color: PropTypes.string,
	href: PropTypes.string,
	size: PropTypes.oneOf(['largest', 'larger', 'smaller', 'smallest', 'inherit']),
	target: PropTypes.oneOf(['_blank', '_self', '_parent', '_top']),
	textAlign: PropTypes.oneOf(['left', 'center', 'right'])
}

BlcLink.defaultProps = {
	children: null,
	color: color.grayDark,
	href: '#',
	size: 'inherit',
	target: null,
	textAlign: 'left'
}

export default BlcLink;
