import React from 'react';
import PropTypes from 'prop-types';
import { color, util, weight } from 'public/styles';

const blcBold = props => (
	<strong>
		{props.children}

		<style jsx>{`
			color: ${props.inverted ? (props.color ? props.color : color.white) : (props.color ? props.color : color.asphalt)};
			display: ${props.display};
			font-size: ${util.blcParagraphSize(props.size)};
			font-weight: ${weight.bold};
			line-height: ${util.blcParagraphLineHeight(props.size)};
			margin: ${props.margin};
			text-align: ${props.textAlign};
		`}</style>
	</strong>
);

blcBold.propTypes = {
	children: PropTypes.node,
	color: PropTypes.string,
	display: PropTypes.oneOf(['inline', 'inline-block', 'block']),
	inverted: PropTypes.bool,
	margin: PropTypes.string,
	size: PropTypes.oneOf(['largest', 'larger', 'smaller', 'smallest', 'inherit']),
	textAlign: PropTypes.oneOf(['left', 'center', 'right'])
}

blcBold.defaultProps = {
	children: null,
	display: 'inline',
	inverted: false,
	margin: '0',
	size: 'inherit',
	textAlign: 'left'
}

export default blcBold;
