import React from 'react';
import PropTypes from 'prop-types';
import { color, size, util, weight } from 'public/styles';

const blcParagraph = props => (
	<p>
		{props.children}

		<style jsx>{`
			color: ${props.color};
			font-family: MarkPro;
			font-size: ${util.blcParagraphSize(props.size)};
			font-stretch: normal;
			font-style: normal;
			font-weight: ${weight.regular};
			letter-spacing: normal;
			line-height: ${util.blcParagraphLineHeight(props.size)};
			margin: ${props.margin} 0;
			text-align: ${props.textAlign};
			white-space: ${props.nowrap ? 'nowrap' : 'normal'};
			width: fit-content;
		`}</style>
	</p>
);

blcParagraph.propTypes = {
	children: PropTypes.node,
	color: PropTypes.string,
	margin: PropTypes.string,
	nowrap: PropTypes.bool,
	size: PropTypes.oneOf(['largest', 'larger', 'smaller', 'smallest', 'inherit']),
	textAlign: PropTypes.oneOf(['left', 'center', 'right'])
}

blcParagraph.defaultProps = {
	children: null,
	color: color.asphalt,
	margin: size.marginSmallest,
	nowrap: false,
	size: null,
	textAlign: 'left'
}

export default blcParagraph;
