import React from 'react';
import PropTypes from 'prop-types';
import { size, weight, color, util } from 'public/styles';

const blcCode = props => {
	if (props.type === 'snippet') {
		return (
			<pre>
				{props.children}

				<style jsx>{`
					background-color: ${props.background};
					color: ${props.color};
					display: block;
					font-family: monospace;
					font-size: ${util.blcParagraphSize(props.size)};
					font-weight: ${weight.regular};
					line-height: ${util.blcParagraphLineHeight(props.size)};
					margin: ${size.marginSmallest} 0;
					padding: ${size.margin};
					text-align: ${props.textAlign};
					width: 100%;
				`}</style>
			</pre>
		)
	} else {
		return (
			<code>
				{props.children}

				<style jsx>{`
					background-color: ${props.background};
					color: ${props.color};
					display: inline-block;
					font-family: monospace;
					font-size: ${util.blcParagraphSize(props.size)};
					font-weight: ${weight.regular};
					line-height: ${util.blcParagraphLineHeight(props.size)};
					margin: 0;
					padding: 0 ${size.marginSmallest};
					text-align: ${props.textAlign};
				`}</style>
			</code>
		)
	}
}

blcCode.propTypes = {
	background: PropTypes.string,
	children: PropTypes.node,
	color: PropTypes.string,
	size: PropTypes.oneOf(['largest', 'larger', 'smaller', 'smallest', 'inherit']),
	textAlign: PropTypes.oneOf(['left', 'center', 'right']),
	type: PropTypes.oneOf(['code', 'snippet'])
}

blcCode.defaultProps = {
	background: color.smoke,
	children: null,
	color: color.alert,
	size: 'inherit',
	textAlign: 'left',
	type: 'code'
}

export default blcCode;
