import React from 'react';
import { weight, color, util } from "public/styles";
import PropTypes from 'prop-types';

export const BlcLinkStyleWrap = props => {
	return (
		<span>
			{props.children}
			<style jsx>{`
				color: ${props.color};
				display: inline-block;
				font-size: ${util.blcParagraphSize(props.size)};
				font-weight: ${weight.regular};
				line-height: ${util.blcParagraphLineHeight(props.size)};
				text-align: ${props.textAlign};
				text-decoration: underline;
				transform: color .15s;

				:active,
				:focus { color: ${props.colorFocus}; }

				:hover { color: ${props.colorHover}; }

				:visited { color: ${props.color}; }
			`}</style>
		</span>
	)
}

BlcLinkStyleWrap.propTypes = {
	children: PropTypes.node,
	color: PropTypes.string,
	colorFocus: PropTypes.string,
	colorHover: PropTypes.string,
	href: PropTypes.string,
	size: PropTypes.oneOf(['largest', 'larger', 'smaller', 'smallest', 'inherit']),
	target: PropTypes.oneOf(['_blank', '_self', '_parent', '_top']),
	textAlign: PropTypes.oneOf(['left', 'center', 'right'])
}

BlcLinkStyleWrap.defaultProps = {
	children: null,
	color: color.grayDark,
	colorFocus: color.asphaltLight,
	colorHover: color.gray,
	href: '#',
	size: 'inherit',
	target: null,
	textAlign: 'left'
}
