import React from 'react';
import PropTypes from 'prop-types';
import { color, size, util, weight } from 'public/styles';

const blcQuote = props => (
	<blockquote cite={props.href} land={props.lang}>
		{props.children}

		<style jsx>{`
			border-left: ${size.border + ' solid ' + (props.inverted ? (props.color ? props.color : color.grayLhight) : (props.color ? props.color : color.grayLight))};
			color: ${props.inverted ? (props.color ? props.color : color.smokeLight) : (props.color ? props.color : color.grayDark)};
			display: ${props.display};
			font-size: ${util.blcParagraphSize(props.size)};
			font-weight: ${weight.regular};
			line-height: ${util.blcParagraphLineHeight(props.size)};
			margin: ${props.margin} 0;
			padding-left: ${size.marginSmaller};
			text-align: ${props.textAlign};
		`}</style>
	</blockquote>
);

blcQuote.propTypes = {
	children: PropTypes.node,
	color: PropTypes.string,
	display: PropTypes.oneOf(['inline', 'inline-block', 'block']),
	href: PropTypes.string,
	inverted: PropTypes.bool,
	lang: PropTypes.string,
	margin: PropTypes.string,
	size: PropTypes.oneOf(['largest', 'larger', 'smaller', 'smallest', 'inherit']),
	textAlign: PropTypes.oneOf(['left', 'center', 'right'])
}

blcQuote.defaultProps = {
	children: null,
	display: 'inline-block',
	href: null,
	inverted: false,
	lang: null,
	margin: size.marginSmaller,
	size: 'inherit',
	textAlign: 'left'
}

export default blcQuote;
